* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

section {
  height: 100vh;
  background-color: black;
  align-items: center;
  justify-content: space-between;
  display: flex;
}
section div {
  border: 2px solid black;
  max-width: 18.75rem;
  max-height: 18.75rem;
}
section img {
  max-width: 18.75rem;
  max-height: 18.75rem;
}
